import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify'
import '@/assets/css/test.css'

import VueCookie from 'vue-cookie'

Vue.config.devtools = true
Vue.use(VueCookie)

new Vue({
  router,
  store,
  vuetify,
  // assets,
  VueCookie,

  render: h => h(App)
}).$mount('#app')
